@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  color: white;
  z-index: 1;
}

a {
  color: rgb(47, 179, 255);
  text-decoration: underline;
}

body {
  background: rgb(21, 21, 34);
  min-height: 100vh;
  font-family: "Inter", sans-serif;
  animation: move 1s linear infinite forwards;
}

html {
  scroll-behavior: smooth;
}

body::before {
  content: "";
  position: fixed;
  height: 200px;
  width: 200px;
  background: rgb(0, 170, 255);
  filter: blur(140px);
}
body::after {
  content: "";
  position: fixed;
  bottom: 0px;
  left: 0px;
  height: 200px;
  width: 200px;
  background: rgb(0, 85, 255);
  filter: blur(140px);
}
.bg-circle1 {
  position: fixed;
  top: 0px;
  right: 0px;
  height: 200px;
  width: 200px;
  background: rgb(0, 255, 204);
  filter: blur(140px);
}

.bg-circle2 {
  position: fixed;
  bottom: 0px;
  right: 0px;
  height: 200px;
  width: 200px;
  background: rgb(119, 0, 255);
  filter: blur(140px);
}

::-webkit-scrollbar {
  width: 0;
}

:root {
  --color-pri: rgb(47, 179, 255);
  --color-pri-light: rgba(0, 145, 255, 0.532);
  --color-sec: rgb(102, 21, 134);
  --color-sec-light: rgba(139, 17, 187, 0.274);

  --pc-width: 85%;
  --tab-width: 90%;
  --phone-width: 100%;
}

.container {
  border-radius: 10px;
  width: var(--pc-width);
  color: var(--text-pri);
  padding: 10px 40px;
  margin: 20px auto;
  padding: 5px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 500px) {
  .container {
    width: var(--phone-width);
  }
}
